import { Component, Input, OnInit } from '@angular/core';
import { SourceCodeService } from '../../source-code.service';

import * as Prism from 'prismjs';
import 'prismjs/components/prism-typescript';

@Component({
  selector: 'app-code-example',
  templateUrl: 'code-example.component.html',
})
export class CodeExampleComponent implements OnInit {
  static STASH_URL =
    'https://stash.se.bedag.ch/projects/ENG-LIBS/repos/bedag-angular-styleguide/browse/frontend/src/app/styleguide';

  /**
   * Base path for the src file names, relative to src/app/styleguide.
   */
  @Input()
  basePath!: string;
  /**
   * A comma separated list of source file names to display.
   * html file names may contain an anchor, to only display the content of the code-example with that anchor (rather than the entire file)
   */
  @Input()
  src!: string;

  showCode = false;
  files!: {
    prettyName: string;
    path: string;
    language: string;
    code: string;
    highlightedCode?: string;
  }[];

  constructor(private readonly sourceCodeService: SourceCodeService) {}

  ngOnInit(): void {
    this.files = this.src.split(',').map((s) => {
      const [name, anchor] = s.split('#');
      const language = name.substring(name.lastIndexOf('.') + 1);
      const prettyName = name.substring(name.lastIndexOf('/') + 1);
      const path = this.basePath ? this.basePath.concat('/', name) : name;
      let code = this.sourceCodeService.sourceCode(path);

      if (anchor) {
        let i = code.indexOf(s);
        if (i === -1) {
          throw new Error(`The source file ${name} does not contain the marker ${s}`);
        } else if (code.indexOf(s, i + 1) !== -1) {
          throw new Error(
            `The source file ${name} contains the marker ${s} more than once. Please specify a unique marker`
          );
        }
        i = code.indexOf('\n', i) + 3;

        const a = code.indexOf('</app-code-example>', i);
        code = code.substring(i, a);
        code = code.split('\n  ').join('\n');
      }
      return { prettyName, path, language, code };
    });
  }

  showOrHide(): void {
    this.showCode = !this.showCode;
    if (this.showCode) {
      for (const file of this.files) {
        file.highlightedCode =
          file.highlightedCode ||
          Prism.highlight(file.code, Prism.languages[file.language], file.language);
      }
    }
  }

  getStashUrl(): string {
    if (this.basePath) {
      return CodeExampleComponent.STASH_URL.concat('/', this.basePath);
    } else {
      const firstFile = this.files[0];
      if (this.files.length === 1) {
        return CodeExampleComponent.STASH_URL.concat('/', this.files[0].path);
      }
      const dir = firstFile.path.substring(0, firstFile.path.lastIndexOf('/') + 1);
      return CodeExampleComponent.STASH_URL.concat('/', dir);
    }
  }
}
